// init handlers
let layoutHandler = null;
let formHandler = null;

const initHandlers = () => {
  layoutHandler = new LayoutHandler();
  formHandler = new FormHandler();
};

// uncomment this to use jQuery
(($) => {
  $(document).ready(() => {
    initHandlers();
  });
})(jQuery);


